.aboutPhoto {
    -webkit-box-shadow: none !important;
            box-shadow: none !important;
    border-radius: 0px !important;
}
.content {
    position: relative;
}
.content>h3 {
    margin-top: 15px;
    margin-bottom: 5px;
}

a:hover {
    color: #23966c;
}

.socialMedia {
	margin-top: 30px;
}
.social {
	text-decoration: none;
    padding-right: 20px;
    display: inline;

}
.social>img {
	-webkit-transition: .3s;
	-o-transition: .3s;
	transition: .3s;
	width: 30px;
	fill: black;
}

#insta:hover {
	fill: #e1306c;
}
#git:hover {
	fill: #6cc644;
}
#youtube:hover {
	fill: #bb0000
}


