/* sm */
@media only screen and (max-width: 768px) {
  .row {
    width: 100vw;
    height: 100vh;
  }

  h1 {
    font-size: 48px;
    line-height: 48px;
  }

  h2 {
    font-size: 36px;
    line-height: 36px;
  }

  h3 {
    font-size: 20px;
    line-height: 20px;
  }

  p {
    width: 100%;
  }

  .actionButton {
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
  }
}

/*Flexbox adjustments */
.col {
  padding: 0px !important;
}


